
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Card from 'vux/src/components/card/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import divider from '@/components/Divider'
import comment from '../good_detail/comment'
import offlinealert from '@/components/OfflineAlert'
const afterserviceArr = ['', '', '', '', '']

export default {
name: 'GoodDetail',
components: {
Swiper,
Group,
Cell,
CellBox,
Flexbox,
FlexboxItem,
Card,
Tab,
TabItem,
Popup,
XNumber,
Toast,
Alert,
divider,
comment,
offlinealert
},
data () {
return {
good_id: '',                // id
role_id: 6,                 // id
show_more: false,           // 
show_count: false,          // 
show_after_service: false,  // 
pop: {
show_service: false       
},
show_offlinegood: false,    // 
show_goodshort_alert: false, // 
show_offline: false,        // 
cart_success: false,        // toast
count_cart: false,          // 
count_now: false,           // 
default_count: false,       // 
show_description: true,     // 
good_detail: {
line_goods: {
is_offline_goods: ''
},
comments: {
total: 0,
latest: {}
},
supplier: {},
attrs: '',
status: 2 
},
offlineInfo: {
address: '',
phone: '1231545412312',
desc: ''
},
banner_list: [],
goodcookie: '',
after_sale_services: [],      // 
user_name: '',
count: 1,                     // 
all_after_sale_services: [],
checkin: 1,                    // 
afterservice_arr: ['', '', '', '', ''],
safeguard_arr: ['', ''],
user_icon: require('../../assets/images/user_icon_default.png'), // 
share_content: [
{image: require('../../assets/images/weixin.png'), title: ''},
{image: require('../../assets/images/pengyouquan.png'), title: ''},
{image: require('../../assets/images/sina.png'), title: ''},
{image: require('../../assets/images/qq.png'), title: 'QQ'},
{image: require('../../assets/images/qzone.png'), title: 'QQ'}
]
}
},
created () {
console.log(this.$route)
this.good_id = this.$route.params.id 
this.axios.get('/mall/goods-view', {id: this.good_id}, (res) => {
this.good_detail = res.data.goods_view

if (this.good_detail.comments.total !== '0') {
this.user_name = this.good_detail.comments.latest.name.substr(0, 6) + '...'
}

this.all_after_sale_services = this.good_detail.after_sale_services
this.after_sale_services = this.good_detail.after_sale_services.slice(0, 3) 

const imgList = this.good_detail.images  
imgList.splice(0, 0, this.good_detail.cover_image)
this.banner_list = imgList.map((item) => ({
img: item
}))
this.afterServiceShow()
})
},
methods: {

afterServiceShow () {
for (let [key, value] of this.all_after_sale_services.entries()) {    // eslint-disable-line
if (afterserviceArr.indexOf(value) !== -1) {
this.pop.show_service = true
}
}
},

tabHandler: function (obj) {
obj === 'des' ? this.show_description = true : this.show_description = false
},


showCount: function (obj) {
if (obj === 'all') {
this.show_count = false
this.count_cart = false
this.count_now = false
this.default_count = false
this.count = 1
} else {
this.show_count = true
obj === 'cart' ? this.count_cart = true : obj === 'now' ? this.count_now = true : this.default_count = true
}
},

showOfflineAlert: function (bool) {
this.show = bool
},

showMore () {
this.show_more = !this.show_more
},

bottomAdd: function (obj) {

this.axios.get('/mall/goods-view', {id: this.good_id}, (res) => {
this.good_detail.status = res.data.goods_view.status
this.good_detail.left_number = res.data.goods_view.left_number
if (this.good_detail.status !== 2) {
this.show_offline = true  
setTimeout(() => {
this.show_offline = false
}, 1500)
} else if (!this.good_detail.left_number) {
this.show_goodshort_alert = true 
} else {
this.showCount(obj)
}
})
},

goodshortSure () {
this.show_goodshort_alert = false
},

addCart () {
this.axios.get('/mall/goods-view', {id: this.good_id}, (res) => {
this.good_detail.status = res.data.goods_view.status
this.good_detail.left_number = res.data.goods_view.left_number
if (this.good_detail.status !== 2) {
this.show_offline = true  
setTimeout(() => {
this.show_offline = false
}, 1500)
} else if (!this.good_detail.left_number) {   //  
this.showCount('all')
this.show_goodshort_alert = true
} else {
this.axios.post('/order/add-shipping-cart', {
goods_id: this.good_id,
goods_num: this.count
}, (res) => {
if (res.code === 200) {
this.showCount('all')
this.cart_success = true 
window.AndroidWebView.showInfoFromJs(res.data)
}
})
}
})
},

buyNow () {
this.axios.get('/mall/goods-view', {id: this.good_id}, (res) => {
this.good_detail.status = res.data.goods_view.status
this.good_detail.left_number = res.data.goods_view.left_number
if (this.good_detail.status !== 2) {
this.show_offline = true  
setTimeout(() => {
this.show_offline = false
}, 1500)
} else if (!this.good_detail.left_number) { //  
this.showCount('all')
this.show_goodshort_alert = true
} else {
/* params
* id 
* */
this.showCount('all')
window.AndroidWebView.skipIntent(this.good_id, this.count)
}
})
},

androidShare () {
window.AndroidWebView.share('' + this.good_detail.title + '' + this.good_detail.subtitle + '' + location.href)
},

skipMessageCenter () {
this.show_more = false
window.AndroidWebView.skipMessageCenter()
},

skipCart () {
window.AndroidWebView.skipShopCart()
},

contactShop () {
this.contactStore(this.good_detail.supplier.uid, this.role_id)
}
}
}



