
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'


export default {
components: {
XHeader,
Search
},
props: {
goLink: {       // backWay  true 
type: String,
default: '/'
},
goParams: {     // 
type: Object,
default: function () {
return {}
}
},
search: '',     // true  
headTitle: '',  // 
pop: '',        // true  more 
backWay: ''     
},
data () {
return {
isShow: false
}
},
methods: {
goHome () {
console.log(this.goLink, this.goParams)
this.$emit('callbackFun')     
this.$router.replace({
path: this.goLink,
params: this.goParams
})
},
showMore () {
this.isShow = !this.isShow

this.$emit('show', this.isShow)
},

skipMessageCenter () {
window.AndroidWebView.skipMessageCenter()
}
}
}



